/* Reset atributtes */
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
}
a {
  text-decoration: none;
}
button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

#root {
  display: grid;
  font-size: 1.6rem;
  gap: 1rem;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 100%;
  justify-content: center;
  margin: auto;
  max-width: 136.6rem;
  min-height: 100vh;
  padding: 1.6rem;
  width: 100%;
}

.video {
  bottom: 0;
  height: 100%;
  left: 0;
  object-fit: cover;
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  z-index: -1;
}
.video__control {
  position: absolute;
  right: 1.6rem;
  top: 1.6rem;
  z-index: 2;
  width: 5rem;
  height: 5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: black;
  transition: 0.4s ease all;
  font-weight: bold;
  border-radius: 50%;
  overflow: hidden;
}
.active {
  background-color: #575d60;
  transition: 0.3s ease-in-out all;
}
.desactive {
  background-color: white;
}
.video__control span {
  position: relative;
  z-index: 2;
  transition: 0.3s ease all;
}

.header {
  display: block;
  position: relative;
  width: 100%;
}
.header__container {
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  text-align: center;
  max-width: 27.6rem;
}
.header__info {
  font-size: 1.4rem /*1.4 probando*/;
  font-weight: 300; /*probando 300 === 100*/
}

.main {
  align-items: center;
  display: flex;
  justify-content: center;
}
.main__container {
  max-width: 29.6rem;
  width: 100%;
}
.main__logo {
  height: max-content;
  object-fit: contain;
  pointer-events: none;
  width: 100%;
}

.footer {
  align-items: flex-end;
  display: grid;
  gap: 1.2rem;
  justify-content: center;
  grid-template-rows: 1fr 1fr 1fr; /*para mobile*/
}

.footer__icon {
  display: flex;
  font-size: 3.6rem;
}

.personal {
  display: grid;
  gap: 1.2rem;
}
.personal__item {
  align-items: center;
  color: white;
  display: flex;
  font-size: 1.8rem;
  gap: 0.5rem;
  justify-content: center;
  padding: 0.2rem;
  background-color: transparent;
}

.work {
  display: grid;
  gap: 1.2rem;
}
.work__item {
  align-items: center;
  color: white;
  display: flex;
  font-size: 1.8rem;
  gap: 0.5rem;
  justify-content: center;
  padding: 0.2rem;
  background-color: transparent;
}

.mail {
  display: flex;
  flex-direction: row;
  overflow: hidden;
}
.mail__close {
  background-color: white;
  border-radius: 50%;
  width: max-content;
  cursor: pointer;
  overflow: hidden;
}
.mail__close:hover {
  background-color: #575d60;
}
.mail__right {
  animation: animation-right 0.3s forwards;
}
.mail__left {
  animation: animation-left 0.3s forwards;
}
.mail__left:hover {
  color: green;
}
@keyframes animation-right {
  0% {
    opacity: 0;
    transform: scale(2) translate(-15rem);
  }
  100% {
    opacity: 1;
    transform: scale(0.9) translate(0);
  }
}
@keyframes animation-left {
  0% {
    opacity: 0;
    transform: scale(2) translate(15rem);
  }
  100% {
    opacity: 1;
    transform: scale(0.9) translate(0);
  }
}

@media (min-width: 904.98px) {
  #root {
    grid-template-columns: 1fr 56rem 1fr;
    grid-template-rows: 1fr;
    padding: 6.4rem;
  }

  .video__control {
    right: 6.4rem;
    top: 6.4rem;
  }

  .header__info {
    font-size: 2.4rem;
  }
  .header__container {
    left: 0;
    position: absolute;
    right: -8rem;
    text-align: start;
    top: 0;
    max-width: 37.6rem;
  }

  .main__container {
    max-width: 56rem;
  }

  .footer {
    position: relative;
    width: 100%;
  }
  .footer__container {
    bottom: 0;
    left: -8rem;
    position: absolute;
    right: 0;
  }
  .footer__title {
    display: block;
    max-width: 14rem;
    opacity: 1; /*Sirve**/
    overflow: hidden;
    text-overflow: ellipsis;
    /* transform: translate(20rem); */
    transition: 0.3s;
    transition: 0.3s ease-out;
    white-space: nowrap;
    width: 100%;
  }

  .personal__list {
    display: flex;
    justify-content: flex-end;
  }
  .personal__item {
    align-items: center;
    color: white;
    display: grid;
    font-size: 2.4rem;
    grid-template-columns: auto /*4rem*/;
    justify-content: flex-end;
    max-width: 18rem;
    overflow: hidden;
    padding: 0.2rem;
    transition: 0.3s;
    width: 100%;
  }
  .personal__item:hover {
    width: 100%;
    /* color: gray; */
  }
  .personal__item:hover .footer__title {
    opacity: 1;
    transform: translate(0);
  }
}
